body{
    text-align: center;
    scroll-behavior: smooth;
    background-color: rgb(179, 241, 241)
}
button:hover{
    animation: blinking 1s linear infinite;
}
@keyframes blinking{
    0%{
        background: linear-gradient(to right,rgb(163, 163, 236),green);
    }
    25%{
        background: linear-gradient(to bottom,rgb(163, 163, 236),rgb(157, 230, 157));
    }
    50%{
        background: linear-gradient(to left,rgb(157, 230, 157),rgb(163, 163, 236));
       
    }
    100%{
        background: linear-gradient(to top,rgb(163, 163, 236),rgb(157, 230, 157));
    }
}
a:hover{
 text-decoration: none;
}
